/* Basic Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html, #root {
  height: 100%;
}

.app {
  display: flex;
  height: 100vh;
  background: #f0f0f0; /* light gray background */
  font-family: Arial, sans-serif;
}

/* Social Sidebar */
.social-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60px;
  background: #333;
}

.social-sidebar a {
  color: #fff;
  font-size: 1.5rem;
  margin: 10px 0;
  transition: transform 0.2s;
}

.social-sidebar a:hover {
  transform: scale(1.2);
}

/* Main Content */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  text-align: center;
}

.logo {
  font-size: 4rem;
  font-weight: bold;
  margin-bottom: 20px;
}

.contact-info {
  font-size: 1.2rem;
  color: #333;
}

.contact-info a {
  color: inherit;
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}

.name {
  margin-bottom: 10px;
  font-weight: bold;
}

.email {
  margin-bottom: 10px;
}

.welcome-message {
  margin-top: 10px;
  font-size: 1.2rem;
  font-weight: 600;
}

/* Bottom Social Links */
.social-links-bottom {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 15px;
}

.social-links-bottom a {
  color: #333;
  font-size: 1.5rem;
  transition: transform 0.2s;
}

.social-links-bottom a:hover {
  transform: scale(1.2);
}

/* Responsive Adjustments */
@media (max-width: 600px) {
  .social-sidebar {
    width: 50px;
  }
  
  .social-sidebar a {
    font-size: 1.2rem;
    margin: 8px 0;
  }
  
  .logo {
    font-size: 3rem;
  }
  
  .contact-info {
    font-size: 1rem;
  }
  
  .welcome-message {
    font-size: 1rem;
  }
  
  .social-links-bottom a {
    font-size: 1.2rem;
  }
}
